.btn {
  font-size: 16px;
  line-height: 25px;
  display: inline-block;
  padding-top: 13px;
  padding-bottom: 15px;
  padding-left: 25px;
  padding-right: 25px;
  text-align: center;
  border-radius: 5px;
  transition: all ease-in 0.3s;
  cursor: pointer;
  text-transform: uppercase;

  @media screen and (max-width: @laptop) {
    padding-top: 8px;
    padding-bottom: 10px;
  }
}


.btn--orange {
  border: 1px solid @orange;
  background-color: @orange;
  color: @white;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.btn--transparent {
  border: 1px solid @white;
  color: @white;
  background-color: transparent;

  &:hover {
    opacity: 0.8;
    border: 1px solid @orange;
    color: @orange;
  }

  &:active {
    opacity: 0.6;
  }
}
