.course {
  border: 1px solid #e1e1e1;
  border-radius: 10px;
  width: 100%;
  max-width: 336px;
  padding: 15px;
  padding-top: 0;
  box-shadow: inset 0px 0px 18px 0px rgba(237, 237, 237, 0.35);

  @media screen and (max-width: @laptop) {
    max-width: 275px;
  }
}

.courses__wrapper {
  padding-top: 40px;
}

.course__img-wrapper {
  width: calc(100% + 30px);
  margin-left: -15px;
  max-height: 260px;
  margin-bottom: 10px;
}

.course__img {
  width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
  border-radius: 10px 10px 0 0;
}

.course__title {
  display: block;
  color: @orange;
  font-size: 18px;
  line-height: 20px;
  padding-right: 50px;
  margin-bottom: 5px;
  position: relative;

  @media screen and (max-width: @laptop) {
    font-size: 16px;
    padding-right: 25px;
  }
}

.course__menthors {
  margin-top: 0;
  margin-bottom: 0;
  color: @gray;
  font-size: 14px;
}

.course__date {
  margin-top: 0;
  margin-bottom: 10px;
  color: @gray;
  font-size: 13px;
  font-weight: bold;

  @media screen and (max-width: @laptop) {
    font-size: 12px;
  }
}

.course__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.course__time {
  text-align: center;
  color: @gray;
  font-weight: bold;
}

.course__hours {
  font-size: 27px;
  line-height: 1;
  color: @gray;
}

.course__price {
  text-align: center;
  color: @orange;
  font-weight: bold;
}

.course__amount {
  font-size: 27px;
  line-height: 1;
}

.course__txt {
  font-size: 16px;
}

.course__btn {
  @media screen and (max-width: @laptop) {
    padding-left: 5px;
    padding-right: 5px;
  }
}

.course--est {
  .course__title {
    &::after {
      content: "";
      display: block;
      width: 24px;
      height: 15px;
      position: absolute;
      top: 0;
      right: 0;
      background-image: url("../img/est-flag.png");
    }
  }
}

.course--rus {
  .course__title {
    &::before {
      content: "";
      display: block;
      width: 24px;
      height: 15px;
      position: absolute;
      top: 18px;
      right: 0;
      background-image: url("../img/rus-flag.png");
    }
  }
}

.courses--black {
  background-color: @black;

  .courses__list-item {
    background-color: @white;
  }
}
