.profile {
}

.profile__wrapper {
  padding-top: 40px;
  padding-bottom: 40px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-content: stretch;

  @media screen and (max-width: @medium) {
    flex-direction: column;
  }
}

.profile__title {
  color: @orange;
  font-size: 36px;
  line-height: 45px;
  text-align: center;
  margin-bottom: 55px;
  width: 100%;

  @media screen and (max-width: @laptop) {
    font-size: 16px;
    padding-right: 25px;
  }
}

.profile__left {
  width: 68%;
  height: 100%;

  @media screen and (max-width: @medium) {
    width: 100%;
    height: auto;
    margin-bottom: 20px;
  }
}

.profile__left-wrapper {
  border: 1px solid #e1e1e1;
  border-radius: 15px;
  padding: 25px;
  box-shadow: inset 0px 0px 18px 0px rgba(237, 237, 237, 0.35);
  box-shadow: inset 0px 0px 18px 0px rgba(237, 237, 237, 0.35);
  box-shadow: inset 0px 0px 18px 0px rgba(237, 237, 237, 0.35);
  margin-bottom: 20px;
}

.profile__inner-top-wrapper {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
}

.profile__sub-title {
  color: @orange;
  font-size: 36px;
  line-height: 45px;
  text-align: center;
}

.profile__list {

}

.profile__list-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;

  .profile__course-name,
  .profile__course-type,
  .profile__btn {
    font-size: 14px;
    line-height: 1.2;
    padding: 12px 24px;
    background-color: #f6f6f6;
    border: 1px solid @white;

    @media screen and (max-width: @small) {
      margin-bottom: 10px;
      text-align: center;
    }
  }

  .profile__course-name {
    max-width: 500px;
    width: 100%;

    @media screen and (max-width: @small) {
      max-width: 100%;
    }
  }

  .profile__course-type {
    max-width: 260px;
    width: 100%;

    @media screen and (max-width: @small) {
      max-width: 100%;
    }
  }

  .profile__btn {
    text-transform: uppercase;
    color: @orange;

    &:hover {
      background-color: @orange;
      color: @white;
    }

    &:active {
      opacity: 0.8;
    }

    @media screen and (max-width: @small) {
      max-width: 100%;
      margin-bottom: 0;
    }
  }

  @media screen and (max-width: @small) {
    flex-direction: column;
    padding: 10px;
    border-radius: 4px;
    border: 1px solid @gray;
  }
}

.profile__right {
  width: 28%;
  display: flex;
  flex-direction: column;

  .profile__reclam {
    width: 100%;
    height: 200px;
    border: 1px solid #e1e1e1;
    border-radius: 15px;
    box-shadow: inset 0px 0px 18px 0px rgba(237, 237, 237, 0.35);
    box-shadow: inset 0px 0px 18px 0px rgba(237, 237, 237, 0.35);
    box-shadow: inset 0px 0px 18px 0px rgba(237, 237, 237, 0.35);
  }

  @media screen and (max-width: @medium) {
    width: 100%;
    height: auto;
    margin-bottom: 20px;
    padding: 0 60px;
  }

  @media screen and (max-width: @small) {
    padding: 0;
  }
}

.profile__logo-wrapper {
  border: 1px solid #e1e1e1;
  border-radius: 15px;
  padding: 25px;
  box-shadow: inset 0px 0px 18px 0px rgba(237, 237, 237, 0.35);
  box-shadow: inset 0px 0px 18px 0px rgba(237, 237, 237, 0.35);
  box-shadow: inset 0px 0px 18px 0px rgba(237, 237, 237, 0.35);
  margin-bottom: 20px;

  @media screen and (max-width: @medium) {
    width: 100%;
    max-width: 450px;
    height: auto;
    margin-bottom: 20px;
    margin-left: auto;
    margin-right: auto;
  }
}

.profile__logo-img {
  height: auto;
  width: 100%;
}

.profile__logo-inner-wrap {
  margin-bottom: 10px;
}

.profile__bold {
  font-size: 20px;
  color: @orange;
  font-weight: bold;
}

.profile__txt {
  font-size: 20px;
  color: @gray;
}

.profile__form {
  margin-bottom: auto;

  @media screen and (max-width: @medium) {
    margin-bottom: 20px;
  }
}

.profile__form-wrapper {
  padding-bottom: 5px;
  margin-bottom: 10px;
}

.profile__label {
  color: #393939;
  font-weight: bold;
  display: block;

  sup {
    color: @orange;
  }
}

.profile__input {
  border: 1px solid #e1e1e1;
  border-radius: 15px;
  padding: 8px;
  box-shadow: inset 0px 0px 18px 0px rgba(237, 237, 237, 0.35);
  box-shadow: inset 0px 0px 18px 0px rgba(237, 237, 237, 0.35);
  box-shadow: inset 0px 0px 18px 0px rgba(237, 237, 237, 0.35);
  width: 100%;
}

.profile__form-btn {
  width: 100%;
}

.profile__reclam {
  position: relative;
  width: 100%;
  height: 160px;
  border: 1px solid #e1e1e1;
  border-radius: 15px;
  box-shadow: inset 0px 0px 18px 0px rgba(237, 237, 237, 0.35);
  box-shadow: inset 0px 0px 18px 0px rgba(237, 237, 237, 0.35);
  box-shadow: inset 0px 0px 18px 0px rgba(237, 237, 237, 0.35);
}

.reclam-baner-img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
  border-radius: 15px;
}
