.footer {
  background-color: #2b2928;
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-top: 100px;
  padding-bottom: 35px;

  @media screen and (max-width: @medium) {
    flex-direction: column;
  }
}

.footer__logo {
  @media screen and (max-width: @medium) {
    margin-bottom: 40px;
  }
}

.footer__list {
  @media screen and (max-width: @medium) {
    margin-bottom: 40px;
  }
}

.footer__list-link {
  color: @white;
  font-size: 20px;
  line-height: 26px;
  transition: all ease 0.3s;

  &:hover,
  &:focus {
    color: @orange;
  }

  &:active {
    opacity: 0.8;
  }
}

.footer__info-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer__info-txt {
  color: @orange;
  font-size: 14px;
  line-height: 23px;
  margin-bottom: 5px;
}

.footer__input {
  font-size: 20px;
  line-height: 29px;
  color: @gray;
  border: none;
  padding-top: 15px;
  padding-bottom: 18px;
  padding-left: 28px;
  padding-right: 28px;
  border-radius: 15px;
  width: 100%;
  margin-bottom: 10px;
  &::placeholder {
    text-align: center;
  }
}

.footer__rights {
  width: 100%;
  color: @white;
  padding-top: 50px;
  text-align: center;
}
