.page-info {
}

.page-info__wrapper {
  padding-top: 40px;
  padding-bottom: 40px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-content: stretch;

  @media screen and (max-width: @medium) {
    flex-direction: column;
  }
}

.page-info__title {
  color: @orange;
  font-size: 36px;
  line-height: 45px;
  text-align: center;
  margin-bottom: 55px;
  width: 100%;

  @media screen and (max-width: @laptop) {
    font-size: 16px;
    padding-right: 25px;
  }
}

.page-info__left {
  width: 68%;
  height: 100%;

  @media screen and (max-width: @medium) {
    width: 100%;
    height: auto;
    margin-bottom: 20px;
  }
}

.page-info__left-wrapper {
  border: 1px solid #e1e1e1;
  border-radius: 15px;
  padding: 25px;
  box-shadow: inset 0 0 18px 0 rgba(237, 237, 237, 0.35);
  margin-bottom: 20px;
}

.page-info__title-inner {
  color: @orange;
  font-size: 16px;
  line-height: 22px;
  margin-bottom: 20px;
}

.page-info__start,
.page-info__dscr {
  font-size: 16px;
  line-height: 22px;
  color: @gray;
  margin-bottom: 20px;
}

.page-info__right {
  width: 28%;
  display: flex;
  flex-direction: column;

  .page-info__reclam {
    width: 100%;
    height: 200px;
    border: 1px solid #e1e1e1;
    border-radius: 15px;
    box-shadow: inset 0 0 18px 0 rgba(237, 237, 237, 0.35);
    box-shadow: inset 0 0 18px 0 rgba(237, 237, 237, 0.35);
    box-shadow: inset 0 0 18px 0 rgba(237, 237, 237, 0.35);
  }

  .Rounded_Rectangle_2_copy {
    border-style: solid;
    border-width: 1px;
    border-color: rgb(181, 181, 181);
    border-radius: 15px;
    background-color: rgb(255, 255, 255);
    opacity: 0.4;
    box-shadow: inset 0px 0px 18px 0px rgba(237, 237, 237, 0.35);
    position: absolute;
    left: 260px;
    top: 1105px;
    width: 958px;
    height: 1565px;
    z-index: 75;
  }


  @media screen and (max-width: @medium) {
    width: 100%;
    height: auto;
    margin-bottom: 20px;
    padding: 0 60px;
  }

  @media screen and (max-width: @small) {
    padding: 0;
  }
}

.page-info__logo-wrapper {
  border: 1px solid #e1e1e1;
  border-radius: 15px;
  padding: 25px;
  box-shadow: inset 0 0 18px 0 rgba(237, 237, 237, 0.35);
  box-shadow: inset 0 0 18px 0 rgba(237, 237, 237, 0.35);
  box-shadow: inset 0 0 18px 0 rgba(237, 237, 237, 0.35);
  margin-bottom: 20px;

  @media screen and (max-width: @medium) {
    width: 100%;
    max-width: 450px;
    height: auto;
    margin-bottom: 20px;
    margin-left: auto;
    margin-right: auto;
  }
}

.page-info__logo-img {
  height: auto;
  width: 100%;
}

.page-info__logo-inner-wrap {
  margin-bottom: 10px;
}

.page-info__bold {
  font-size: 20px;
  color: @orange;
  font-weight: bold;
}

.page-info__txt {
  font-size: 20px;
  color: @gray;
}

.page-info__course {
  border: 1px solid #e1e1e1;
  border-radius: 15px;
  padding: 25px;
  box-shadow: inset 0 0 18px 0 rgba(237, 237, 237, 0.35);
  box-shadow: inset 0 0 18px 0 rgba(237, 237, 237, 0.35);
  box-shadow: inset 0 0 18px 0 rgba(237, 237, 237, 0.35);
  margin-bottom: 20px;

  .page-info__bold {
    font-size: 16px;
    display: block;
    margin-bottom: 10px;
  }

  .page-info__txt {
    display: inline-block;
    margin-bottom: 10px;
  }

  .course {
    margin: 0 auto;

    .course__wrapper {
      flex-wrap: wrap;
      justify-content: space-around;
    }

    .course__btn {
      width: 100%;
    }
  }
}

.page-info__course-title {
  font-weight: bold;
  font-size: 20px;
  line-height: 1.2;
  color: @orange;
  text-align: center;
  display: block;
}

.page-info__course-name {
  text-align: center;
  font-size: 20px;
  line-height: 1.2;
  color: @gray;
}

.page-info__course-link {
  width: 100%;
  color: @white;
  background-color: @gray;
  margin-bottom: 20px;
}

.page-info__course-btn {
  width: 100%;
}

.page-info__reclam {
  position: relative;
  width: 100%;
  height: 160px;
  border: 1px solid #e1e1e1;
  border-radius: 15px;
  box-shadow: inset 0 0 18px 0 rgba(237, 237, 237, 0.35);
  box-shadow: inset 0 0 18px 0 rgba(237, 237, 237, 0.35);
  box-shadow: inset 0 0 18px 0 rgba(237, 237, 237, 0.35);
}

.reclam-baner-img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
  border-radius: 15px;
}
