.login {
  width: 100%;
  max-width: 540px;
  display: none;
}

.login__title {
  display: block;
  text-align: center;
  font-size: 20px;
  line-height: 29px;
  margin-bottom: 15px;
}

.login__input {
  font-size: 14px;
  line-height: 29px;
  color: #666;
  border: none;
  border-radius: 15px;
  padding: 5px 18px;
}

input[type=email].login__input {
  width: 100%;
}

.login__form-inner-wrapper {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
  height: 60px;
  border-bottom: 2px solid @white;
  padding-bottom: 21px;
  margin-bottom: 20px;

  .login__input {
    flex-grow: 1;
    margin-right: 10px;
  }

  .login__sabmit {
    padding-top: 7px;
  }
}

.login__registration-btn {
  width: 100%;
}
