.registration {
  width: 100%;
  max-width: 540px;

  @media screen and (max-width: @laptop) {
    top: 100px;
    transform: translate(-50%, 0);
  }

  @media screen and (max-width: @extra-large) {
    top: 50%;
    transform: translate(-50%, -50%);
  }
}

.registration__title {
  display: block;
  text-align: center;
  font-size: 20px;
  line-height: 29px;
  margin-bottom: 15px;
}

.registration__radio {
  display: none;
}

.registration__radio-label {
  width: 220px;
  height: 115px;
  border: 2px solid @white;
  border-radius: 15px;
  display: block;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  position: relative;
  padding-top: 10px;
}

.registration__radio-label--student {
  &::before {
    content: "";
    display: block;
    width: 54px;
    height: 67px;
    background-image: url("../img/student.png");
    background-size: cover;
    background-position: 50%;
    background-repeat: no-repeat;
    margin: 0 auto;
    margin-bottom: 7px;
  }
}

.registration__radio-label--organization {
  &::before {
    content: "";
    display: block;
    width: 67px;
    height: 67px;
    background-image: url("../img/school.png");
    background-size: cover;
    background-position: 50%;
    background-repeat: no-repeat;
    margin: 0 auto;
    margin-bottom: 7px;
  }
}

.registration__radio:checked + .registration__radio-label {
  background-color: @orange;
}

.registration__radios-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media screen and (max-width: @medium) {
    flex-direction: column;

    .registraion__checkbox-wrapper {
      margin-bottom: 10px;
    }
  }
}

.registaration__info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
}

.registaration__input-wrapper {
  @media screen and (max-width: @medium) {
    width: 100%;
  }
}

.registration__label {
  display: block;
  font-size: 14px;
  line-height: 32px;
  font-weight: bold;
  padding-left: 20px;

  sup {
    color: @orange;
  }
}

.registration__input {
  font-size: 14px;
  line-height: 29px;
  color: #666666;
  border: none;
  border-radius: 15px;
  padding: 5px 18px;

  @media screen and (max-width: @medium) {
    width: 100%;
  }
}

.registaration__input-wrapper--email {
  .registration__input {
    width: 100%;
  }
}

.registration__input-file-wrapper {
  position: relative;
  width: 206px;
  height: 39px;
  background-color: @orange;
  border-radius: 15px;
  text-align: center;
  padding-top: 7px;

  &::before {
    content: "Загрузите логотип";
  }
}

.registration__input--file {
  display: block;
  position: absolute;
  width: 100%;
  font-size: 0;
  opacity: 0;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
}

.registration__consent {
  margin-top: 20px;
}

.registration__consent-checkbox {
  display: none;
}

.registration__consent-label {
  font-size: 12px;
  padding-left: 40px;
  display: block;
  position: relative;
  margin-bottom: 10px;

  &::before {
    content: "";
    display: block;
    width: 25px;
    height: 25px;
    background-color: @white;
    border-radius: 50%;
    position: absolute;
    top: -4px;
    left: 0;
    transition: all ease 0.3s;
  }
}

.registration__consent-checkbox:checked + .registration__consent-label {
  &::before {
    content: "";
    display: block;
    width: 25px;
    height: 25px;
    background-color: @orange;
    border-radius: 50%;
    position: absolute;
    top: -4px;
    left: 0;
  }
}

.registration__capcha-wrapper {
  display: flex;
  height: 56px;

  @media screen and (max-width: @medium) {
    flex-wrap: wrap;
    justify-content: space-between;
    min-height: 56px;
    height: auto;
  }
}

.registration__capcha {
  font-size: 28px;
  padding-left: 10px;
  padding-right: 10px;

  @media screen and (max-width: @medium) {
    padding-top: 8px;
    margin: 0;
  }
}

.registration__capcha-input {
  width: 130px;
  border-radius: 5px;
  padding-left: 10px;
  padding-right: 10px;
  text-align: center;
  margin-right: 10px;
  border: none;

  @media screen and (max-width: @medium) {
    height: 40px;
  }
}

.registration__sabmit {
  padding-left: 5px;
  padding-right: 5px;

  @media screen and (max-width: @medium) {
    margin-top: 15px;
    width: 100%;
  }
}
