}
.btn {
  font-size: 16px;
  line-height: 25px;
  display: inline-block;
  padding-top: 13px;
  padding-bottom: 15px;
  padding-left: 25px;
  padding-right: 25px;
  text-align: center;
  border-radius: 5px;
  transition: all ease-in 0.3s;
  cursor: pointer;
  text-transform: uppercase;
}
