}
.btn--orange {
  border: 1px solid #ff6d34;
  background-color: #ff6d34;
  color: #ffffff;
}
.btn--orange:hover {
  opacity: 0.8;
}
.btn--orange:active {
  opacity: 0.6;
}
.btn--transparent {
  border: 1px solid #ffffff;
  color: #ffffff;
  background-color: transparent;
}
.btn--transparent:hover {
  opacity: 0.8;
  border: 1px solid #ff6d34;
  color: #ff6d34;
}
.btn--transparent:active {
  opacity: 0.6;
