.header {
  background-image: url('../img/main-bg.jpg');
  background-size: 100% 900px;
  background-position: 0 0;
  background-repeat: no-repeat;
  min-height: 945px;
  padding-top: 50px;

  @media screen and (max-width: @laptop) {
    background-size: cover;
    background-position: 50%;
    padding-bottom: 50px;
  }

  @media screen and (max-width: @medium) {
    background-size: cover;
    background-position: 40%;
    padding-bottom: 50px;
  }
}

.header__wrapper-top {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media screen and (max-width: @extra-large) {
    flex-wrap: wrap;
  }
}

.header__languages {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: auto;
  margin-right: 70px;

  @media screen and (max-width: @extra-large) {
    width: 50%;
    justify-content: flex-end;
    margin-right: 0;
  }

  @media screen and (max-width: @small) {
    width: 100%;
    justify-content: space-around;
    margin-top: 20px;
    display: none;
  }
}

.header__languages-item {
  margin-right: 20px;

  @media screen and (max-width: @small) {
    margin-right: 0;
  }
}

.header__languages-link {
  color: @white;
  font-size: 20px;
  line-height: 29px;
  padding-left: 30px;
  position: relative;
  transition: all ease 0.3s;

  &::after {
    content: "";
    display: block;
    width: 24px;
    height: 15px;
    position: absolute;
    top: 5px;
    left: 0;
  }

  &:hover,
  &:focus {
    color: @orange;
  }

  &:active {
    opacity: 0.8;
  }
}

.header__languages-item--est {
  .header__languages-link {
    &::after {
      background-image: url("../img/est-flag.png");
    }
  }
}

.header__languages-item--rus {
  .header__languages-link {
    &::after {
      background-image: url("../img/rus-flag.png");
    }
  }
}

.header__user {

  min-width: 300px;

  @media screen and (max-width: @extra-large) {
    margin-left: auto;
    margin-top: 20px;
  }

  @media screen and (max-width: @small) {
    margin-left: 0;
    max-width: 100%;
    display: none;
  }
}

.header__user-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__user-link {
  font-size: 20px;
}

.header__user-logged-list {
  display: none;

  .header__user-list-item {
    margin: 5px;
  }
}

.header__user--logged {
  .header__user-list {
    display: none;
  }

  .header__user-logged-list {
    display: flex;
  }
}

.header__wrapper-bottom {
  padding-top: 215px;

  @media screen and (max-width: @laptop) {
    padding-top: 155px;
  }

  @media screen and (max-width: @small) {
    padding-top: 40px;
  }
}

.header__info {
  width: 670px;
  margin-left: auto;
  text-align: right;
  margin-bottom: 115px;

  @media screen and (max-width: @medium) {
    width: 100%;
    text-align: center;
    margin-bottom: 40px;
  }
}

.header__slogan {
  font-size: 50px;
  line-height: 58px;
  color: @white;
  margin-left: auto;
  font-weight: 400;

  @media screen and (max-width: @small) {
    font-size: 38px;
    line-height: 48px;
  }
}

.header__dscr {
  font-size: 26px;
  line-height: 1.5;
  color: @white;
  padding-left: 100px;

  @media screen and (max-width: @small) {
    padding-left: 0;
    font-size: 16px;
  }
}

.header__mobile-btn {
  height: 29px;
  width: 30px;
  position: absolute;
  top: 0;
  right: 15px;
  background-color: transparent;
  border: none;
  outline: none;

  @media screen and (min-width: @small) {
    display: none;
  }
}

.header__mobile-btn--gamburger {
  animation: scale 0.3s;

  height: 29px;
  width: 30px;

  &::after {
    content: "";
    display: block;
    width: 100%;
    height: 3px;
    background-color: @orange;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 1px;
  }

  &::before {
    content: "";
    display: block;
    width: 100%;
    height: 3px;
    background-color: @orange;
    position: absolute;
    bottom: 0;
    left: 0;
    border-radius: 1px;
  }

  .header__mobile-btn-decor {
    display: block;
    width: 80%;
    height: 3px;
    background-color: @orange;
    position: absolute;
    top: 13px;
    right: 0;
    border-radius: 1px;
  }
}

.header__mobile-btn--close {
  animation: scale2 0.4s;

  &::after {
    content: "";
    display: block;
    width: 31px;
    height: 3px;
    background-color: @orange;
    position: absolute;
    top: 13px;
    left: 0;
    transform: rotate(45deg);
    border-radius: 1px;
  }

  &::before {
    content: "";
    display: block;
    width: 31px;
    height: 3px;
    background-color: @orange;
    position: absolute;
    bottom: 13px;
    left: 0;
    transform: rotate(135deg);
    border-radius: 1px;
  }

  .header__mobile-btn-decor {
    display: none;
  }
}

@keyframes scale {
  0% {
    transform: scale(0);
  }

  70% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes scale2 {
  0% {
    transform: scale(0);
  }

  70% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}
