.filters {
  @media screen and (max-width: @small) {
    position: absolute;
    z-index: 10;
    width: 92%;
    left: 50%;
    transform: translateX(-50%);
  }
}

.filters__wrapper {
  width: 100%;
}

.filters__title {
  color: @orange;
  font-size: 36px;
  line-height: 46px;
  text-align: center;
}

.filters__form {
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 20px;
  padding: 20px 36px;

  @media screen and (max-width: @laptop) {
    background-color: #000000;
  }
}

.filters__label {
  color: @orange;
  display: block;
  font-size: 20px;
  font-weight: bold;
  line-height: 29px;
  margin-bottom: 10px;
  padding-left: 28px;

  @media screen and (max-width: @laptop) {
    padding-top: 8px;
    padding-bottom: 8px;
    padding-left: 18px;
    padding-right: 18px;

    font-size: 14px;
    line-height: 23px;
  }
}

.filters__select-wrapper {
  width: 100%;
  max-width: 225px;
  margin-right: 20px;
  margin-left: 20px;

  @media screen and (max-width: @medium) {
    max-width: 180px;
    margin-right: 5px;
    margin-left: 5px;
  }

  @media screen and (max-width: @small) {
    max-width: 100%;
    text-align: center;
  }

  @media screen and (max-width: @small) {
    margin-left: 0;
    margin-right: 0;
  }
}

.filters__input-select {
  width: 100%;
  font-size: 20px;
  line-height: 29px;
  color: @gray;
  border: none;
  padding-top: 15px;
  padding-bottom: 18px;
  padding-left: 28px;
  padding-right: 28px;
  border-radius: 15px;

  @media screen and (max-width: @laptop) {
    padding-top: 12px;
    padding-bottom: 12px;
    padding-left: 18px;
    padding-right: 18px;

    font-size: 14px;
    line-height: 23px;
  }
}

.filters__form-top-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;

  .filters__select-wrapper:last-child {
    max-width: 100%;
    flex-grow: 1;
  }

  @media screen and (max-width: @laptop) {
    align-items: flex-end;
  }

  @media screen and (max-width: @medium) {
    flex-wrap: wrap;
  }

  @media screen and (max-width: @small) {
    flex-direction: column;
  }
}

.filters__form-middle-wrapper {
  display: none;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;

  @media screen and (max-width: @extra-large) {
    flex-wrap: wrap;
  }
}

.filters__date-title {
  color: @orange;
  display: block;
  font-size: 20px;
  font-weight: bold;
  line-height: 29px;
  margin-bottom: 10px;
  padding-left: 28px;
  text-align: center;
  margin-top: 0;

  @media screen and (max-width: @laptop) {
    padding-top: 8px;
    padding-bottom: 8px;
    padding-left: 18px;
    padding-right: 18px;

    font-size: 14px;
    line-height: 23px;
  }

  @media screen and (max-width: @extra-large) {
    width: 100%;
  }
}

.filters__date-wrapper {
  @media screen and (max-width: @extra-large) {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    max-width: 600px;
  }

  @media screen and (max-width: @medium) {
    max-width: 380px;
  }

  @media screen and (max-width: @small) {
    // flex-direction: column;
  }
}

.filters__text-input {
  font-size: 20px;
  line-height: 29px;
  color: @gray;
  border: none;
  padding-top: 15px;
  padding-bottom: 18px;
  padding-left: 28px;
  padding-right: 28px;
  border-radius: 15px;
  width: 100%;
  max-width: 220px;

  @media screen and (max-width: @laptop) {
    padding-top: 8px;
    padding-bottom: 8px;
    padding-left: 18px;
    padding-right: 18px;

    font-size: 14px;
    line-height: 23px;
    max-width: 140px;
  }

  @media screen and (max-width: @extra-large) {
    max-width: 250px;
  }

  @media screen and (max-width: @medium) {
    max-width: 160px;
  }

  @media screen and (max-width: @small) {
    max-width: 110px;
  }
}

.filters__label-date {
  color: @orange;
  font-size: 20px;
  font-weight: bold;
  line-height: 29px;
  width: 100%;
  max-width: 270px;

  @media screen and (max-width: @laptop) {
    font-size: 14px;
    line-height: 23px;
  }

  @media screen and (max-width: @extra-large) {
    max-width: 48%;
  }

  @media screen and (max-width: @small) {
    max-width: 48%;
  }
}

.filters__count-wrapper {
  span {
    display: inline-block;
    padding: 0 15px;
    color: @white;
    font-size: 20px;
  }

  @media screen and (max-width: @extra-large) {
    width: 100%;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
  }

  @media screen and (max-width: @medium) {
    max-width: 400px;
  }

  .filters__text-input {
    @media screen and (max-width: @small) {
      max-width: 100%;
      margin-bottom: 20px;
    }
  }

  span {
    @media screen and (max-width: @small) {
      display: none;
    }
  }
}

.filters__individ-label {
  display: inline-block;
  color: @orange;
  font-size: 14px;
  line-height: 18px;
  padding-left: 45px;
  position: relative;
  vertical-align: sub;

  &::after {
    content: "";
    display: block;
    width: 35px;
    height: 35px;
    border: 2px solid @orange;
    border-radius: 15px;
    position: absolute;
    top: 0;
    left: 0;
    transition: all ease-in 0.3s;
  }
}

.filters__checkbox {
  display: none;
}

.filters__checkbox:checked + .filters__individ-label {
  &::after {
    content: "";
    display: block;
    width: 35px;
    height: 35px;
    border: 1px solid @orange;
    background-color: @orange;
    border-radius: 15px;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.filters__form-bottom-wrapper {
  display: none;
  justify-content: space-around;
  align-items: center;

  .filters__range {
    width: 35%;
    margin: 0 auto;
  }

  input[type=range] {
    width: 100%;
  }

  @media screen and (max-width: @small) {
    flex-direction: column;

    .filters__range {
      width: 100%;
    }
  }
}

.filters__submit {
  display: block;
  margin-left: auto;
}

.filters__detail {
  position: relative;
  background-color: transparent;
  border: none;
  color: @white;
  padding-right: 20px;
  font-size: 20px;
  line-height: 29px;
  display: block;
  width: 188px;
  margin-left: auto;
  margin-right: auto;
  cursor: pointer;

  &::after {
    content: "";
    display: block;
    width: 10px;
    height: 9px;
    position: absolute;
    top: 12px;
    right: 0;
    background-image: url("../img/arrow-down.png");
  }

  @media screen and (max-width: @small) {
    margin: 0 auto;
  }
}

.filters__detail-back {
  position: relative;
  background-color: transparent;
  border: none;
  color: @white;
  padding-right: 20px;
  font-size: 20px;
  line-height: 29px;
  display: block;
  width: 118px;
  margin-left: auto;
  cursor: pointer;
  margin-bottom: 10px;

  &::after {
    content: "";
    display: block;
    width: 10px;
    height: 9px;
    position: absolute;
    top: 12px;
    right: 0;
    background-image: url("../img/arrow-down.png");
    transform: rotate(-180deg);
  }
}

.filters--avtive {
  .filters__form-bottom-wrapper,
  .filters__form-middle-wrapper {
    display: flex;
  }
  transition: all ease 0.3s;
}
