.popup {
  display: none;
  position: absolute;
  padding: 35px;
  padding-bottom: 45px;
  background-color: #000000;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 15px;
  color: @white;
  z-index: 11;

  @media screen and (max-width: @medium) {
    transform: translate(-50%, 0);
  }
}

.popup__btn {
  display: block;
  height: 46px;
  width: 46px;
  padding: 5px;
  position: absolute;
  top: 5px;
  right: 15px;
  background-color: transparent;
  border: 2px solid @orange;
  outline: none;
  border-radius: 50%;
  animation: scale2 0.4s;
  transition: all ease-in 0.3;

  &::after {
    content: "";
    display: block;
    width: 32px;
    height: 2px;
    background-color: @orange;
    position: absolute;
    top: 20px;
    left: 5px;
    transform: rotate(45deg);
    border-radius: 1px;
  }

  &::before {
    content: "";
    display: block;
    width: 32px;
    height: 2px;
    background-color: @orange;
    position: absolute;
    bottom: 20px;
    left: 5px;
    transform: rotate(135deg);
    border-radius: 1px;
  }

  &:hover {
    background-color: @orange;
    border: 2px solid @white;
    transition: all ease-in 0.3;

    &::after {
      content: "";
      display: block;
      width: 32px;
      height: 2px;
      background-color: @white;
      position: absolute;
      top: 20px;
      left: 5px;
      transform: rotate(45deg);
      border-radius: 1px;
    }

    &::before {
      content: "";
      display: block;
      width: 32px;
      height: 2px;
      background-color: @white;
      position: absolute;
      bottom: 20px;
      left: 5px;
      transform: rotate(135deg);
      border-radius: 1px;
    }
  }

  &:active {
    opacity: 0.8;
  }
}
