.courses__wrapper {
  padding-bottom: 50px;

  @media screen and (max-width: @small) {
    padding-top: 60px;
  }
}

.courses__title {
  color: @orange;
  font-size: 36px;
  line-height: 45px;
  text-align: center;
  margin-bottom: 55px;

  @media screen and (max-width: @small) {
    margin-bottom: 20px;
  }
}

.courses__list {
  display: flex;
  flex-wrap: wrap;

  .courses__list-item {
    margin-right: 18px;
    margin-bottom: 18px;

    &:nth-child(4n) {
      margin-right: 0;

      @media screen and (max-width: @extra-large) {
        margin: 20px auto;
      }
    }

    @media screen and (max-width: @laptop) {
      margin-right: 10px;
    }

    @media screen and (max-width: @extra-large) {
      margin: 20px auto;
    }
  }


  @media screen and (max-width: @extra-large) {
    justify-content: space-around;
  }
}

