.main-form__title {
  font-size: 36px;
  line-height: 45px;
  color: @orange;
  text-align: center;

  @media screen and (max-width: @laptop) {
    font-size: 26px;
    line-height: 35px;
  }
}

.main-form__wrapper-inner {
  padding: 40px 68px;
  border: 1px solid #e1e1e1;
  border-radius: 15px;
  box-shadow: inset 0px 0px 18px 0px rgba(237, 237, 237, 0.35);
  box-shadow: inset 0px 0px 18px 0px rgba(237, 237, 237, 0.35);
  box-shadow: inset 0px 0px 18px 0px rgba(237, 237, 237, 0.35);
  margin-bottom: 40px;

  @media screen and (max-width: @laptop) {
    margin-bottom: 20px;
  }

  @media screen and (max-width: @small) {
    padding: 20px 28px;
  }
}

.main-form__input-wrapper {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.main-form__wrapper-inner--trainig-place,
.main-form__wrapper-inner--contact-person,
.main-form__wrapper-inner--organization-info {
  display: flex;
  flex-wrap: wrap;

  .main-form__input-wrapper {
    width: 100%;
    max-width: 390px;
    margin-right: 46px;

    &:nth-child(3n) {
      margin-right: 0;
    }

    @media screen and (max-width: @laptop) {
      max-width: 314px;
      margin-right: 30px;
    }

    @media screen and (max-width: 1024px) {
      max-width: 30%;
      margin-right: auto;
    }

    @media screen and (max-width: @medium) {
      max-width: 100%;
    }
  }

  .main-form__input-wrapper--full-width {
    max-width: 100%;
  }
}

.main-form__label {
  font-weight: bold;
  color: @orange;
  font-size: 20px;
  display: block;
  margin-bottom: 5px;

  @media screen and (max-width: @laptop) {
    font-size: 16px;
  }

  @media screen and (max-width: 1024px) {
    font-size: 14px;
    font-weight: normal;
  }
}

.main-form__input {
  border: 1px solid #e1e1e1;
  border-radius: 15px;
  padding: 15px;
  box-shadow: inset 0px 0px 18px 0px rgba(237, 237, 237, 0.35);
  width: 100%;
  background-color: transparent;

  @media screen and (max-width: @laptop) {
    padding-top: 12px;
    padding-bottom: 12px;
    padding-left: 18px;
    padding-right: 18px;

    font-size: 14px;
    line-height: 23px;
  }
}
